"use client"

import { useEffect, useState } from "react"
import { Sidebar } from "@/components/sidebar"
import { Header } from "@/components/header"
import { MetricsRow } from "@/components/dashboard/metrics-row"
import { FilterBar } from "@/components/dashboard/filter-bar"
import { UploadCard } from "@/components/dashboard/upload-card"
import { FileList } from "@/components/dashboard/file-list"
import { RecentUsers } from "@/components/dashboard/recent-users"
import { useCRMStore } from "@/lib/store"
import { sampleProtectRecords, sampleSettlementRecords, calculateDashboardStats } from "@/lib/sample-data"
import { toast } from "sonner"
import type { UploadHistory } from "@/lib/types"

export default function DashboardPage() {
  const { records, uploadHistory, setRecords, addRecords } = useCRMStore()
  const [isHydrated, setIsHydrated] = useState(false)

  // Initialize with sample data if empty
  useEffect(() => {
    setIsHydrated(true)
    if (records.length === 0) {
      setRecords([...sampleProtectRecords, ...sampleSettlementRecords])
    }
  }, [records.length, setRecords])

  const stats = calculateDashboardStats(records, uploadHistory)

  const handleUpload = async (file: File, type: "protect" | "settlement") => {
    // Simulate API upload
    return new Promise<void>((resolve) => {
      setTimeout(() => {
        const newUpload: UploadHistory = {
          id: `upload-${Date.now()}`,
          fileName: file.name,
          type,
          recordCount: Math.floor(Math.random() * 50) + 10,
          uploadedAt: new Date().toISOString(),
          uploadedBy: "Current User",
          status: "success",
        }

        // Add dummy records for this upload
        // In a real app, these would come from the parsed file
        addRecords([], newUpload)
        toast.success(`${type === "protect" ? "Protect" : "Settlement"} file uploaded successfully`)
        resolve()
      }, 2000)
    })
  }

  const handleDeleteFile = (id: string) => {
    // Implement delete logic
    toast.success("File deleted successfully")
  }

  const handleViewFile = (id: string) => {
    // Implement view logic
  }

  if (!isHydrated) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="animate-pulse text-muted-foreground">Loading...</div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <Sidebar />
      <main className="ml-64">
        <Header title="Dashboard" subtitle="Overview of your loan settlement operations" />

        <div className="p-6 space-y-6">
          {/* Filters */}
          <FilterBar />

          {/* Metrics */}
          <MetricsRow stats={stats} />

          {/* Main Content Grid */}
          <div className="grid grid-cols-1 lg:grid-cols-5 gap-6">
            {/* Left Column (60%) */}
            <div className="lg:col-span-3 space-y-6">
              <div className="space-y-4">
                <h3 className="text-lg font-semibold">Protect</h3>
                <UploadCard type="protect" onUpload={(f) => handleUpload(f, "protect")} />
                <FileList
                  files={uploadHistory}
                  type="protect"
                  onDelete={handleDeleteFile}
                  onView={handleViewFile}
                />
              </div>
            </div>

            {/* Right Column (40%) */}
            <div className="lg:col-span-2 space-y-6">
              <div className="space-y-4">
                <h3 className="text-lg font-semibold">Settlement</h3>
                <UploadCard type="settlement" onUpload={(f) => handleUpload(f, "settlement")} />
                <FileList
                  files={uploadHistory}
                  type="settlement"
                  onDelete={handleDeleteFile}
                  onView={handleViewFile}
                />
              </div>

              <div className="pt-4">
                <RecentUsers />
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  )
}
